* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Inter, sans-serif;
  font-size: .75em;
  line-height: 1.5;
}

a {
  color: #212529;
  text-decoration: none;
}

p {
  margin: 0;
}

.gallery-container {
  padding-bottom: 15px;
  padding-left: 15px;
  padding-right: 15px;
}

.search-container {
  height: 7%;
  width: 100%;
  z-index: 1;
  background-color: #063;
  justify-content: center;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  position: fixed;
  box-shadow: 2px 2px 4px rgba(33, 37, 41, .35);
}

.search-form {
  width: 100%;
  justify-content: center;
  display: flex;
}

.search-form__input {
  min-width: 40%;
  max-width: 60%;
  background-color: #edf1e9;
  border: none;
  border-radius: 5px 0 0 5px;
  outline: none;
  padding: 10px;
  font-size: 1.25em;
  font-weight: 300;
}

.search-form__input:hover, .search-form__input:focus {
  background-color: #f5f7f3;
}

.search-form__button {
  min-width: 4.5em;
  width: 6em;
  cursor: pointer;
  background-color: #6dae81;
  border: none;
  border-radius: 0 4px 4px 0;
  justify-content: center;
  align-items: center;
  font-size: 1.25em;
  font-weight: 500;
  display: flex;
}

.search-form__button:hover, .search-form__button:focus {
  background-color: #47a76a;
  transition: all .5s;
}

.gallery, .info {
  flex-wrap: wrap;
  display: flex;
}

.gallery {
  justify-content: center;
  margin-left: -20px;
  padding-top: 60px;
}

.gallery-link {
  min-width: 500px;
  border: none;
  border-radius: 5px;
  flex-basis: 20%;
  margin-top: 20px;
  margin-left: 20px;
  transition: all .4s;
  overflow: hidden;
  box-shadow: 2px 2px 4px rgba(33, 37, 41, .35);
}

.gallery-link:hover, .gallery-link:focus {
  transform: scale(1.03);
  box-shadow: 4px 4px 8px rgba(33, 37, 41, .35);
}

.gallery-item {
  flex-direction: column;
  justify-content: space-evenly;
  display: flex;
}

.gallery-item__image {
  height: 300px;
  object-fit: cover;
  border-bottom: 1px solid rgba(61, 43, 31, .3);
  display: block;
}

.info {
  background-color: #edf1e9;
  justify-content: space-around;
  padding: 10px;
}

.info__item {
  flex-direction: column;
  align-items: center;
  font-weight: 500;
  display: flex;
}

.info__item-name {
  font-weight: 700;
}

.load-button.is-hidden {
  transform: all .3s;
  display: none;
}

.load-button {
  min-width: 8em;
  color: #fff;
  transform: scale(1.05) .3s;
  background-color: #063;
  border: none;
  border-radius: 5px;
  margin: 30px auto 0;
  padding: 10px;
  font-size: 1.25em;
  font-weight: 500;
  display: block;
  box-shadow: 2px 2px 4px rgba(33, 37, 41, .35);
}

